"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Upload, File, Check, X, Loader2 } from "lucide-react"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "~/components/ui/card"
import { Button } from "~/components/ui/button"
import { toast } from "sonner"

interface FileObject {
  file: File
  id: number
  status: "pending" | "uploading" | "success" | "error"
}

const FileUploader = () => {
  const [files, setFiles] = useState<FileObject[]>([])
  const [message, setMessage] = useState<string>("")
  const [isUploading, setIsUploading] = useState<boolean>(false)
  const [uploadStatus, setUploadStatus] = useState<"" | "success" | "error" | "partial">("")
  const [dragOver, setDragOver] = useState<boolean>(false)
  const router = useRouter()

  useEffect(() => {
    const token = localStorage.getItem("token")
    if (!token || token === "") {
      router.push("/404") // ⬅️ Redirect
    }
  }, [router])

  // Toast messages
  useEffect(() => {
    if (!isUploading && message) {
      if (uploadStatus === "success") toast.success(message)
      else if (uploadStatus === "error") toast.error(message)
      else if (uploadStatus === "partial") toast.warning(message)
      else toast.info(message)
      setMessage("")
    }
  }, [message, uploadStatus, isUploading])

  const addFiles = (newFiles: File[]) => {
    const validFiles = newFiles.filter(
      (file) => !files.some((f) => f.file.name === file.name && f.file.size === file.size),
    )

    if (validFiles.length > 0) {
      setFiles((prev) => [
        ...prev,
        ...validFiles.map((file) => ({
          file,
          id: Date.now() + Math.random(),
          status: "pending" as const,
        })),
      ])
      setMessage("")
      setUploadStatus("")
    } else if (newFiles.length > 0) {
      setMessage("Some files were already selected.")
      setUploadStatus("")
    }
  }

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFiles = Array.from(e.target.files || [])
    addFiles(selectedFiles)
  }

  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    setDragOver(false)
    addFiles(Array.from(e.dataTransfer.files))
  }

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    setDragOver(true)
  }

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    setDragOver(false)
  }

  const removeFile = (fileId: number) => {
    setFiles((prev) => prev.filter((f) => f.id !== fileId))
  }

  const clearAllFiles = () => setFiles([])

  const handleUpload = async () => {
    if (files.length === 0) {
      setMessage("Please select at least one file first.")
      setUploadStatus("error")
      return
    }

    setIsUploading(true)
    setMessage(`Uploading ${files.length} file(s)...`)
    setUploadStatus("")

    let successCount = 0
    let errorCount = 0

    for (const fileObj of files) {
      setFiles((prev) => prev.map((f) => (f.id === fileObj.id ? { ...f, status: "uploading" } : f)))

      const formData = new FormData()
      formData.append("file", fileObj.file)

      const token = localStorage.getItem("token")

      try {
        const response = await fetch("/api/load", {
          method: "POST",
          headers: {
            Authorization: token ? `Bearer ${token}` : "",
          },
          body: formData,
        })

        let data: any
        const text = await response.text()

        try {
          data = JSON.parse(text)
          console.log("Parsed JSON response:", data)
        } catch {
          data = { message: text }
          console.log("Non-JSON response:", data)
        }

        console.log("Raw response:", response)

        if (response.ok) {
          setFiles((prev) => prev.map((f) => (f.id === fileObj.id ? { ...f, status: "success" } : f)))
          successCount++
          console.log("Upload success:", data)
        } else {
          console.error("Backend error:", data)
          setFiles((prev) => prev.map((f) => (f.id === fileObj.id ? { ...f, status: "error" } : f)))
          errorCount++
        }
      } catch (error) {
        console.error(`Error uploading file ${fileObj.file.name}:`, error)
        setFiles((prev) => prev.map((f) => (f.id === fileObj.id ? { ...f, status: "error" } : f)))
        errorCount++
      }

      await new Promise((resolve) => setTimeout(resolve, 300))
    }

    setIsUploading(false)

    if (successCount === files.length) setUploadStatus("success")
    else if (errorCount === files.length) setUploadStatus("error")
    else setUploadStatus("partial")

    setMessage(`${successCount} file(s) uploaded successfully, ${errorCount} failed.`)
  }

  const handleContinue = () => router.push("/qa")

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return "0 Bytes"
    const k = 1024
    const sizes = ["Bytes", "KB", "MB", "GB"]
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return Number.parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + " " + sizes[i]
  }

  return (
    <div className="flex min-h-screen items-center justify-center p-4">
      <Card className="w-full max-w-lg border-0 shadow-sm dark:bg-zinc-950/20">
        <CardHeader className="text-center pb-6">
          <CardTitle className="text-2xl font-medium text-balance">Upload Files</CardTitle>
          <CardDescription className="text-muted-foreground">Drag and drop files or click to browse</CardDescription>
        </CardHeader>

        <CardContent className="space-y-6">
          <div
            className={`relative border-2 border-dashed rounded-lg p-8 text-center transition-colors ${
              dragOver ? "border-primary bg-primary/5" : "border-muted-foreground/25 hover:border-muted-foreground/50"
            }`}
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
          >
            <Upload className="mx-auto h-8 w-8 text-muted-foreground mb-4" />
            <div className="space-y-2">
              <p className="text-sm font-medium">Drop files here</p>
              <p className="text-xs text-muted-foreground">or</p>
              <label className="inline-flex items-center justify-center rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 border border-input bg-background hover:bg-accent hover:text-accent-foreground h-10 px-4 py-2 cursor-pointer">
                Browse Files
                <input type="file" multiple onChange={handleFileChange} className="hidden" />
              </label>
            </div>
            {files.length > 0 && (
              <p className="text-xs text-muted-foreground mt-4">
                {files.length} file{files.length !== 1 ? "s" : ""} selected
              </p>
            )}
          </div>

          {files.length > 0 && (
            <div className="space-y-3">
              <div className="flex items-center justify-between">
                <h3 className="text-sm font-medium">Selected Files</h3>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={clearAllFiles}
                  disabled={isUploading}
                  className="h-8 px-2 text-xs text-red-600 cursor-pointer "
                >
                  Clear all
                </Button>
              </div>

              <div className="space-y-2 max-h-48 overflow-y-auto">
                {files.map((fileObj) => (
                  <div key={fileObj.id} className=" dark:bg-zinc-950/20 flex items-center gap-3 p-3 rounded-md border bg-card">
                    <div className="flex-shrink-0">
                      {fileObj.status === "success" ? (
                        <div className="h-5 w-5 rounded-full bg-green-100 flex items-center justify-center">
                          <Check className="h-3 w-3 text-green-600" />
                        </div>
                      ) : fileObj.status === "uploading" ? (
                        <Loader2 className="h-5 w-5 animate-spin text-muted-foreground" />
                      ) : fileObj.status === "error" ? (
                        <div className="h-5 w-5 rounded-full bg-red-100 flex items-center justify-center">
                          <X className="h-3 w-3 text-red-600" />
                        </div>
                      ) : (
                        <File className="h-5 w-5 text-muted-foreground" />
                      )}
                    </div>

                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium truncate">{fileObj.file.name}</p>
                      <p className="text-xs text-muted-foreground">{formatFileSize(fileObj.file.size)}</p>
                    </div>

                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => removeFile(fileObj.id)}
                      disabled={isUploading}
                      className="h-8 w-8 p-0"
                    >
                      <X className="h-4 w-4" />
                    </Button>
                  </div>
                ))}
              </div>
            </div>
          )}

          {isUploading ? (
            <div className="flex items-center justify-center gap-2 p-3 rounded-md bg-muted">
              <Loader2 className="h-4 w-4 animate-spin" />
              <span className="text-sm text-muted-foreground">{message}</span>
            </div>
          ) : (
            <Button
              onClick={uploadStatus === "success" ? handleContinue : handleUpload}
              disabled={files.length === 0}
              className="w-full"
              size="lg"
            >
              {uploadStatus === "success" ? "Continue" : `Upload ${files.length} file${files.length !== 1 ? "s" : ""}`}
            </Button>
          )}
        </CardContent>
      </Card>
    </div>
  )
}

export default FileUploader
